/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  list-style: none;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.header {
  background-image: url(../images/header.png);
  background-size: cover;
  height: 50vh;
  opacity: 0.6;
  display: flex;
  flex-direction: column-reverse;
  align-items: center; }
  .header__search {
    padding: 10px 100px;
    background-color: white;
    z-index: 1;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; }
  .header__input {
    padding: 5px;
    margin-right: 15px;
    width: 250px;
    border: 3px solid rgba(91, 228, 226, 0.35); }
    .header__input:focus {
      outline: none;
      border: 3px solid rgba(26, 234, 106, 0.722); }
  .header__btn {
    background-color: rgba(91, 228, 226, 0.35);
    color: grey;
    border-style: none;
    padding: 7px;
    border-radius: 15%; }

.main {
  display: grid;
  grid-template-columns: 1fr 3fr; }

.allCharacters {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .allCharacters__title {
    margin: 20px;
    color: rgba(246, 214, 52, 0.875);
    font-size: 24px;
    font-family: "Kanit", sans-serif; }
    .allCharacters__title--icon {
      width: 20px; }
  .allCharacters__list {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center; }
    .allCharacters__list--article {
      box-shadow: rgba(246, 214, 52, 0.875) 2px 2px 9px 3px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;
      margin-bottom: 5px; }

.allfavs {
  background-color: pink; }

.hidden {
  display: none; }

.favsCharacters {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .favsCharacters__list {
    margin-bottom: 20px; }
    .favsCharacters__list--article {
      box-shadow: rgba(182, 34, 18, 0.875) 2px 2px 9px 3px;
      background-color: rgba(182, 34, 18, 0.875);
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;
      margin-bottom: 5px; }

.removeFav {
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  padding: 7px;
  border-radius: 60%;
  background-color: rgba(217, 209, 209, 0.882);
  font-size: 12px;
  color: white; }

.resetBtn {
  width: 100px;
  background-color: rgba(63, 212, 117, 0.78);
  color: grey;
  border-style: none;
  padding: 7px;
  border-radius: 10%; }
